.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;

}

.dialogs{
    display: block;
    padding: 10px;
    border: 2px solid teal;
    margin: 10px;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: teal;
}

.dialogItem .dialog {
    padding: 10px;
    text-decoration: none;
    color: brown;


}

.dialogItem .active {
    color: gold;


}


.item img {
    width: 30px;
    height: 30px;
    border-radius: 20px;

}

.item{
    display: flex;
    padding: 15px;
    margin-top: 15px;
    justify-content: space-between;
    align-items: center;
    width: 80%;
    background-color: teal;
}
.imgItem {
    display: block;
    margin-right: 15px;
    justify-content: space-between;
    align-items: center;
}
.avaName{
    margin: 15px;
}

.btnItem {
    display : flex;
    justify-content: right;
}


